import { SyntheticEvent, useState } from "react";
import AddressAutocomplete from "../../components/AddressAutocomplete/AddressAutocomplete";
import ExclamationMark from "../../components/ExclamationMark/ExclamationMark";
import StyledCheckboxes from "../../components/StyledInputs/StyledCheckboxes";
import StyledTextArea from "../../components/StyledInputs/StyledTextArea";
import StyledRadio from "../../components/StyledInputs/StyledRadio";
import StyledInput from "../../components/StyledInputs/StyledInput";
import DotSpinner from "../../components/DotSpinner/DotSpinner";
import { IStandardServicePath } from "../../core/types/types";
import { helpers } from "../../core/services/helpers";
import { motion } from "framer-motion";
import {
  animationVariants,
  FixedServiceRequestTypes,
  FixedServiceTypes,
} from "../../core/constants/constants";

const StandardServicePath: React.FC<IStandardServicePath> = ({
  serviceRequest,
  updateServiceRequest,
  onSubmit,
  checkboxErrorMessage,
  updateCheckboxErrorMessage,
  isLoading,
  formErrorMessage,
  assessmentNoError,
  updateAssessmentNoError,
  customerNoError,
  updateCustomerNoError,
}) => {
  const [addressErrorMessage, updateAddressErrorMessage] = useState<string | null>(null);
  const [isValidAddress, updateIsValidAddress] = useState(false);
  const [onCollectionTime, updateOnCollectionTime] = useState<boolean | null>(null);
  const [hasContaminatedSticker, updateHasContaminatedSticker] = useState<boolean | null>(null);
  const [isCorrectWeek, updateIsCorrectWeek] = useState<boolean | null>(null);

  // Update service list in service request state when a checkbox is ticked or unticked
  function handleCheckboxChange(e: SyntheticEvent) {
    updateCheckboxErrorMessage(null);

    const value = (e.target as HTMLInputElement).value;
    if (serviceRequest.service === null) {
      updateServiceRequest({
        ...serviceRequest,
        service: [value],
      });
    } else {
      if (!serviceRequest.service?.includes(value)) {
        updateServiceRequest({
          ...serviceRequest,
          service: [...serviceRequest.service, value],
        });
      } else {
        updateServiceRequest({
          ...serviceRequest,
          service: serviceRequest.service.filter((item) => item !== value),
        });
      }
    }
  }

  return (
    <motion.form
      variants={animationVariants}
      initial="initial"
      animate="in"
      exit="out"
      className="flex flex-col items-center w-full"
      onSubmit={onSubmit}
    >
      <motion.div variants={animationVariants} className="w-full">
        <StyledCheckboxes
          serviceRequest={serviceRequest}
          label={
            serviceRequest.serviceRequest === FixedServiceRequestTypes.ADDITIONAL_BIN
              ? "Select the services that relate to your request. Please note that you must be a ratepayer to request any additional bins."
              : "Select the services that relate to your request."
          }
          onChange={(e) => {
            handleCheckboxChange(e);
          }}
          serviceList={
            serviceRequest.serviceRequest === FixedServiceRequestTypes.ADDITIONAL_BIN
              ? FixedServiceTypes.serviceListWithGardenWasteSignUp
              : FixedServiceTypes.serviceList
          }
          checkboxErrorMessage={checkboxErrorMessage}
        />
      </motion.div>
      {serviceRequest.serviceRequest === FixedServiceRequestTypes.DAMAGED_BIN ? (
        <motion.div variants={animationVariants} className="w-full">
          <StyledTextArea
            label="Please describe the damage and add any additional comments"
            required={true}
            onChange={(e) =>
              updateServiceRequest({
                ...serviceRequest,
                comments: (e.target as HTMLInputElement).value,
              })
            }
          />
        </motion.div>
      ) : (
        <motion.div variants={animationVariants} className="w-full">
          <StyledTextArea
            label="Add any additional comments (optional)"
            required={false}
            onChange={(e) =>
              updateServiceRequest({
                ...serviceRequest,
                comments: (e.target as HTMLInputElement).value,
              })
            }
          />
        </motion.div>
      )}
      <motion.div variants={animationVariants} className="w-full">
        <StyledInput
          label="Enter your full name"
          onChange={(e) =>
            updateServiceRequest({
              ...serviceRequest,
              name: (e.target as HTMLInputElement).value,
            })
          }
        />
      </motion.div>
      <motion.div variants={animationVariants} className="w-full">
        <StyledInput
          label="Enter your email address"
          type="email"
          onChange={(e) =>
            updateServiceRequest({
              ...serviceRequest,
              emailAddress: (e.target as HTMLInputElement).value,
            })
          }
        />
      </motion.div>
      <motion.div variants={animationVariants} className="w-full">
        <StyledInput
          label="Enter your phone number"
          type="number"
          onChange={(e) =>
            updateServiceRequest({
              ...serviceRequest,
              phoneNo: (e.target as HTMLInputElement).value,
            })
          }
        />
      </motion.div>
      {serviceRequest.serviceRequest === FixedServiceRequestTypes.ADDITIONAL_BIN && (
        <div className="w-full">
          <motion.div variants={animationVariants} className="w-full">
            <StyledInput
              label="Enter your rates valuation number (no special characters)"
              required={false} // custom validation used instead
              onChange={(e) => {
                updateAssessmentNoError(null);
                updateServiceRequest({
                  ...serviceRequest,
                  assessmentNumber: (e.target as HTMLInputElement).value,
                });
              }}
            />
          </motion.div>
          {assessmentNoError && (
            <motion.div
              variants={animationVariants}
              className="mb-4 bg-white inline-block rounded w-full"
            >
              <div className="m-2 p-2 relative border border-red-500 rounded flex items-center justify-end">
                <p className="mr-0 flex-grow text-red-500 ">{assessmentNoError}</p>
                <div className="flex-shrink-0 mr-2">
                  <ExclamationMark />
                </div>
              </div>
            </motion.div>
          )}
          <motion.div variants={animationVariants} className="w-full">
            <StyledInput
              label="Enter your customer number (no special characters)"
              required={false} // custom validation used instead
              type="number"
              onChange={(e) => {
                updateCustomerNoError(null);
                updateServiceRequest({
                  ...serviceRequest,
                  customerNumber: (e.target as HTMLInputElement).value,
                });
              }}
            />
          </motion.div>
          {customerNoError && (
            <motion.div
              variants={animationVariants}
              className="mb-4 bg-white inline-block rounded w-full"
            >
              <div className="m-2 p-2 relative border border-red-500 rounded flex items-center justify-end">
                <p className="mr-0 flex-grow text-red-500 ">{customerNoError}</p>
                <div className="flex-shrink-0 mr-2">
                  <ExclamationMark />
                </div>
              </div>
            </motion.div>
          )}
        </div>
      )}
      {serviceRequest.serviceRequest === FixedServiceRequestTypes.MISSED_COLLECTION && (
        <div className="w-full">
          <motion.div variants={animationVariants} className="w-full">
            <StyledRadio
              id="collectionTimeRadio"
              label="Was your bin at the collection point by 7:00am?"
              onChange={(e) =>
                updateOnCollectionTime(
                  helpers.convertStrToBool((e.target as HTMLInputElement).value)
                )
              }
            />
          </motion.div>
          {onCollectionTime === false && (
            <motion.div
              variants={animationVariants}
              className="mb-4 bg-white inline-block rounded w-full"
            >
              <div className="m-2 p-2 relative border border-red-500 rounded flex items-center justify-end">
                <p className="mr-0 flex-grow text-red-500 ">
                  Bins need to be at the collection point by 7am to ensure they are collected.
                  Please put your bin(s) out by 7am on your next collection day.
                </p>
                <div className="flex-shrink-0 mr-2">
                  <ExclamationMark />
                </div>
              </div>
            </motion.div>
          )}
          <motion.div variants={animationVariants} className="w-full">
            <StyledRadio
              id="contaminationStickerRadio"
              label="Does your bin contain a non-collection tag or a contamination sticker?"
              onChange={(e) =>
                updateHasContaminatedSticker(
                  helpers.convertStrToBool((e.target as HTMLInputElement).value)
                )
              }
            />
          </motion.div>
          {hasContaminatedSticker === true && (
            <motion.div
              variants={animationVariants}
              className="mb-4 bg-white inline-block rounded w-full"
            >
              <div className="m-2 p-2 relative border border-red-500 rounded flex items-center justify-end">
                <p className="mr-0 flex-grow text-red-500 ">
                  Please refer to the tag/sticker applied to your bin for instructions.
                </p>
                <div className="flex-shrink-0 mr-2">
                  <ExclamationMark />
                </div>
              </div>
            </motion.div>
          )}
          <motion.div variants={animationVariants} className="w-full">
            <StyledRadio
              id="correctWeekRadio"
              label="Have you double checked that you have put your bin out on the correct collection day/week?"
              onChange={(e) =>
                updateIsCorrectWeek(helpers.convertStrToBool((e.target as HTMLInputElement).value))
              }
            />
          </motion.div>
          {isCorrectWeek === false && (
            <motion.div
              variants={animationVariants}
              className="mb-4 bg-white inline-block rounded w-full"
            >
              <div className="m-2 p-2 border border-red-500 rounded flex items-center justify-end">
                <p className="mr-2 flex-grow text-red-500 ">
                  Please refer to the collection calendar to find out what bins go out on which
                  week. You will need to check the side of your bin to see if you fall in week 1 or
                  week 2.
                </p>
                <div className="flex-shrink-0 mr-2">
                  <ExclamationMark />
                </div>
              </div>
            </motion.div>
          )}
          <motion.div variants={animationVariants} className="w-full">
            <StyledRadio
              id="otherBinsMissedRadio"
              label="Have any other bins been missed on your street?"
              onChange={(e) => {
                updateServiceRequest({
                  ...serviceRequest,
                  missedCollectionDetails: {
                    ...serviceRequest.missedCollectionDetails,
                    otherBinsMissed: helpers.convertStrToBool((e.target as HTMLInputElement).value),
                  },
                });
              }}
            />
          </motion.div>
          <motion.div variants={animationVariants} className="w-full">
            <StyledRadio
              id="mudRadio"
              label="Do you live in a retirement village or apartment complex?"
              onChange={(e) =>
                updateServiceRequest({
                  ...serviceRequest,
                  missedCollectionDetails: {
                    ...serviceRequest.missedCollectionDetails,
                    isMUD: helpers.convertStrToBool((e.target as HTMLInputElement).value),
                  },
                })
              }
            />
          </motion.div>
        </div>
      )}
      <motion.div variants={animationVariants} className="w-full">
        <AddressAutocomplete
          label="Enter your address"
          serviceRequest={serviceRequest}
          updateServiceRequest={updateServiceRequest}
          addressErrorMessage={addressErrorMessage}
          updateAddressErrorMessage={updateAddressErrorMessage}
          isValidAddress={isValidAddress}
          updateIsValidAddress={updateIsValidAddress}
        />
      </motion.div>
      {serviceRequest.serviceRequest === FixedServiceRequestTypes.ADDITIONAL_BIN ? (
        <motion.div variants={animationVariants} className="mb-4 h-full w-full flex flex-col">
          <p className="border border-gray-300 shadow-sm px-3 py-3 rounded text-sm bg-white text-gray-800">
            In clicking ‘Submit’, I confirm I am the ratepayer of this property, or authorised by
            the ratepayer to submit this request on their behalf. I have read and agree to the{" "}
            <a
              href="https://www.envirowaste.co.nz/assets/TCCSRPORTAL/Website-Privacy-Statement.pdf"
              className="underline"
            >
              Privacy Statement
            </a>
            ,{" "}
            <a
              href="https://www.envirowaste.co.nz/assets/TCCSRPORTAL/Website-App-Terms-of-Use.pdf"
              className="underline"
            >
              Website/App Terms of Use
            </a>{" "}
            and (as applicable) the{" "}
            <a
              href="https://www.envirowaste.co.nz/assets/TCCSRPORTAL/Terms-and-Conditions-for-Additional-Bins.pdf"
              className="underline"
            >
              Additional Bins Terms and Conditions
            </a>{" "}
            or the{" "}
            <a
              href="https://www.tauranga.govt.nz/Portals/0/data/living/rubbish_recycling/files/garden-waste-terms-conditions.pdf"
              className="underline"
            >
              Garden Waste Terms and Conditions
            </a>
            .
          </p>
        </motion.div>
      ) : (
        <motion.div variants={animationVariants} className="mb-4 h-full w-full flex flex-col">
          <p className="border border-gray-300 shadow-sm px-3 py-3 rounded bg-white text-sm text-gray-800">
            In clicking ‘Submit’, I confirm I have read and agree to the{" "}
            <a
              href="https://www.envirowaste.co.nz/assets/TCCSRPORTAL/Website-Privacy-Statement.pdf"
              className="underline"
            >
              Privacy Statement
            </a>{" "}
            and{" "}
            <a
              href="https://www.envirowaste.co.nz/assets/TCCSRPORTAL/Website-App-Terms-of-Use.pdf"
              className="underline"
            >
              Website/App Terms of Use
            </a>
            .
          </p>
        </motion.div>
      )}
      {formErrorMessage && (
        <motion.div variants={animationVariants} className="mb-4 bg-white rounded w-full">
          <div className="m-2 p-2 relative border border-red-500 rounded flex items-center justify-end">
            <p className="mr-0 flex-grow text-red-500 ">{formErrorMessage}</p>
            <div className="flex-shrink-0 mr-2">
              <ExclamationMark />
            </div>
          </div>
        </motion.div>
      )}
      <motion.div variants={animationVariants} className="w-full flex justify-end">
        <button
          type="submit"
          disabled={
            addressErrorMessage !== null ||
            checkboxErrorMessage !== null ||
            assessmentNoError !== null ||
            customerNoError !== null ||
            onCollectionTime === false ||
            isCorrectWeek === false ||
            hasContaminatedSticker === true
          }
          className="body-font disabled-button-style w-28 xl:w-40 text-white bg-tcc-green transition duration-300 ease-in-out hover:bg-tcc-green-lighter rounded py-2.5 md:py-3 focus:outline-none border border-transparent focus:border-gray-800"
        >
          {isLoading ? <DotSpinner /> : "Submit"}
        </button>
      </motion.div>
    </motion.form>
  );
};

export default StandardServicePath;
