import GoogleMapReact from "google-map-react";
import Marker from "../Marker/Marker";
import { useEffect, useMemo, useState } from "react";
import { ILatLng, IMap } from "../../core/types/types";

const Map: React.FC<IMap> = ({ serviceRequest, updateServiceRequest }) => {
  const initialLatLng = { lat: -37.7020457, lng: 176.2463033 };
  const [isDraggable, updateIsDraggable] = useState(true);
  const [markerLatLng, updateMarkerLatLng] = useState<ILatLng| null>(null); // prettier-ignore
  const [zoomPosition, updateZoomPosition] = useState(10);
  const [centerPosition, updateCenterPosition] = useState<ILatLng>(initialLatLng);
  const serviceRequestLatLng = useMemo(() => {
    return {
      lat: serviceRequest.addressComponents.latitude,
      lng: serviceRequest.addressComponents.longitude,
    };
  }, [serviceRequest.addressComponents]);

  // Updates marker latLng, camera position and zoom whenever the service request's latLng is changed
  //? Not ideal, this means that marker position is updated twice when marker is dragged
  //? Also, zoom doesn't change if a user has manually adjusted the zoom
  useEffect(() => {
    if (serviceRequestLatLng.lat !== null && serviceRequestLatLng.lng !== null) {
      updateMarkerLatLng({ lat: serviceRequestLatLng.lat, lng: serviceRequestLatLng.lng });
      updateCenterPosition(serviceRequestLatLng);
      updateZoomPosition(18);
    }
  }, [serviceRequestLatLng]);

  return (
    <div className="w-full mb-4">
      <div className="h-80 w-full relative">
        <p className="hidden sm:block bg-white p-2 text-base text-gray-500 rounded border border-gray-100 shadow whitespace-nowrap absolute top-2 right-2 z-10 opacity-90">
          Drag and drop the marker for greater accuracy
        </p>
        <GoogleMapReact
          bootstrapURLKeys={{ key: "AIzaSyDBUKNe73SRE3Tr1K1crTDO1Jrw6FKSlS0" }}
          center={
            centerPosition.lat == null || centerPosition.lng == null
              ? initialLatLng
              : { lat: centerPosition.lat, lng: centerPosition.lng }
          }
          zoom={zoomPosition}
          draggable={isDraggable}
          options={{ scrollwheel: true, fullscreenControl: false }}
          // on mousedown, stop map from being draggable
          onChildMouseDown={() => updateIsDraggable(false)}
          // on mousemove, update the markers latLng
          onChildMouseMove={(hoverKey, childProps, mouse) => updateMarkerLatLng({ lat: mouse.lat, lng: mouse.lng })} // prettier-ignore
          // on mouseup, make map draggable and update the service request's latLng
          onChildMouseUp={() => {
            updateIsDraggable(true);
            markerLatLng &&
              updateServiceRequest({
                ...serviceRequest,
                addressComponents: {
                  ...serviceRequest.addressComponents,
                  latitude: markerLatLng.lat,
                  longitude: markerLatLng.lng,
                },
              });
          }}
        >
          {markerLatLng && (
            <Marker
              lat={markerLatLng.lat ?? initialLatLng.lat}
              lng={markerLatLng.lng ?? initialLatLng.lng}
            />
          )}
        </GoogleMapReact>
      </div>
    </div>
  );
};

export default Map;
