// Handles all entry and exit animations for the service request paths
export const animationVariants = {
  initial: {
    opacity: 0,
    scale: 0.9,
  },
  in: {
    opacity: 1,
    scale: 1,
    transition: {
      ease: "easeInOut",
      duration: 0.2,
      staggerChildren: 0.075,
    },
  },
  out: {
    opacity: 0,
    transition: { duration: 0.075 },
  },
};
